.footer {
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: flex-start;
  .footerContainer {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    .container {
      display: flex;
      justify-content: flex-start;
      text-align: center;
      width: 250px;
      margin-top: 20px;
      .box {
        height: 30px;
        width: 30px;
        background-color: #483d8b;
        margin-right: 5px;
        color: #fff;
      }
    }
  }
}




